import { IQJsonAdditionals, ModelAdditionals, ModelType } from "./quickCloud";

type ValueOf<Type> = Type extends any ? Type[keyof Type] : never;
type FieldValueOf<Type extends typeof typeSafety, Field extends keyof Type[keyof Type]> = Type[keyof Type][Field]
export type EditorModelTypes = ValueOf<FieldValueOf<typeof EditorConsts, "modelKeys">>;
let typeSafety: Record<string, { modelKeys: Record<string, string> }>;
export const EditorConsts = typeSafety = {
    quick: {
        modelKeys: {
            self: "qjson" as "qjson",
        }
    },
    flow: {
        modelKeys: {
            design: "flow-design" as "flow-design",
            runtime: "flow-runtime" as "flow-runtime",
        }
    },
    flowEntity: {
        modelKeys: {
            single: "flow-entity" as "flow-entity",
        }
    },
};

interface ModelVisualInfo {
    icon: string;
    color: string;
}
const cssJsTsYaml: ModelVisualInfo = { color: "#449DD1", icon: "mdi mdi-cog-outline" };
const jpegJpgPngSvgGif: ModelVisualInfo = { color: "#212121", icon: "mdi mdi-image-outline " }
const modelVisuals: Record<ModelType, ModelVisualInfo | Error | ((additional: ModelAdditionals | undefined) => ModelVisualInfo)> = {
    qjson: (additionals) => {
        if (additionals && (additionals as IQJsonAdditionals).qjsonType == "quickComponent") {
            return { color: "#449DD1", icon: "mdi mdi-shape-outline" };
        }
        return { color: "#449DD1", icon: "mdi mdi-palette" };
    },
    css: { color: "#151233", icon: "mdi mdi-music-accidental-sharp" },
    js: cssJsTsYaml,
    ts: cssJsTsYaml,
    yaml: cssJsTsYaml,
    entityDesigner: { color: "#E9724C", icon: "mdi mdi-database" },
    bpmn: { color: "#007C77", icon: "mdi mdi-vector-polyline" },
    process: { color: "#007C77", icon: "mdi mdi-auto-fix" },
    flow: { color: "#007C77", icon: "mdi mdi-step-forward" },
    flowEntity: { color: "blue", icon: "mdi mdi-step-forward" },
    namedComponent: { color: "#212121", icon: "mdi mdi-cube-outline" },
    step: new Error("unimplemented for namedComponent"),
    jpeg: jpegJpgPngSvgGif,
    jpg: jpegJpgPngSvgGif,
    png: jpegJpgPngSvgGif,
    svg: jpegJpgPngSvgGif,
    gif: jpegJpgPngSvgGif,
    woff: { color: "#007C77", icon: "mdi-format-font," },
    woff2: { color: "#007C77", icon: "mdi-format-font," },
    ttf: { color: "#007C77", icon: "mdi-format-font," },
    lottie: jpegJpgPngSvgGif,
    appSettings: new Error("unimplemented for appSettings"),
    placeholder: { color: "#212121", icon: "mdi mdi-folder-open" },
    json: new Error("unimplemented for json"),
}

export class ModelContants {
    public static getModelVisualInfo(modelType: ModelType, modelAdditionals: ModelAdditionals | undefined): ModelVisualInfo {
        const retVal = modelVisuals[modelType];
        if (retVal instanceof Error) { throw retVal; }
        return typeof retVal == "function" ? retVal(modelAdditionals) : retVal;
    }
}